import PropTypes from 'prop-types';
import { ForwardControl } from 'video-react';

class FrameStepControl extends ForwardControl { }

FrameStepControl.propTypes = {
  seconds: PropTypes.oneOf([-0.2, -0.04, 0.04, 0.2, 5, 10, 30]).isRequired,
};

export default FrameStepControl;



