import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Player, ControlBar } from 'video-react';
import FrameStepControl from './FrameStepControl';
import HLSSource from './HLSSource';
import config from '../../config';
import './Video.css';

class Video extends Component {
  constructor(props, context) {
    super(props, context);
    this.playerRef = React.createRef();

    this.state = {
      poster: props.activeSource.poster,
      source: props.activeSource.video,
      hls: false,
    };
    this.selectHls = this.selectHls.bind(this);
    this.selectMp4 = this.selectMp4.bind(this);
  }

  componentDidMount() {
    // subscribe state change
    this.playerRef.current.subscribeToStateChange(this.handleStateChange.bind(this));
  }

  shouldComponentUpdate(nextProps) {
    if (nextProps.activeSource !== this.props.activeSource) {
      this.setState({
        poster: nextProps.activeSource.poster,
        source: nextProps.activeSource.video,
      });
      this.playerRef.current.load();
    }
    return true;
  }

  selectHls() {
    this.setState({
      poster: null,
      source: 'http://www.streambox.fr/playlists/x36xhzz/x36xhzz.m3u8',
      hls: true,
    });
  }

  selectMp4() {
    this.setState({
      poster: this.props.activeSource.poster,
      source: this.props.activeSource.video,
      hls: false,
    });
  }

  handleStateChange(state) {
    const currentFrame = parseInt(state.currentTime * config.frameRate, 10);
    if (currentFrame !== this.state.currentFrame) {
      // eslint-disable-next-line
      // console.log('player state: ', state.currentTime, currentFrame, state.duration);
      this.setState({
        player: state,
        currentFrame,
      });
    }
  }

  render() {
    return (
      <div>
        {this.state.hls ? (
          <button onClick={this.selectMp4}>Use MP4</button>
        ) : (
          <button onClick={this.selectHls}>Use HLS</button>
        )}
        <Player
          ref={this.playerRef}
          fluid={false}
          width={config.videoWidth}
          height={config.videoHeight}
          {...(this.state.hls ? {} : {
            poster: this.state.poster,
            src: this.state.source,
          })}
        >
          {this.state.hls && (
            <HLSSource
              isVideoChild
              src={this.state.source}
            />
          )}
          <ControlBar autoHide={false}>
            <FrameStepControl seconds={-(5 / config.frameRate)} order={3.1} />
            <FrameStepControl seconds={-(1 / config.frameRate)} order={3.2} />
            <FrameStepControl seconds={1 / config.frameRate} order={3.3} />
            <FrameStepControl seconds={5 / config.frameRate} order={3.4} />
          </ControlBar>
        </Player>
      </div>
    );
  }
}

Video.propTypes = {
  activeSource: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  activeSource: state.main.activeSource,
});

export default connect(
  mapStateToProps,
)(Video);



