import config from '../config';

export const SOURCE_LIST_LOAD = 'main/SOURCE_LIST_LOAD';
export const SOURCE_LIST_SET_ACTIVE_SOURCE = 'main/SOURCE_LIST_SET_ACTIVE_SOURCE';

const initialState = {
  sourceList: [],
  activeSource: null,
};


export default (state = initialState, action) => {
  switch (action.type) {
    case SOURCE_LIST_LOAD:
      return {
        ...state,
        sourceList: action.payload,
      };

    case SOURCE_LIST_SET_ACTIVE_SOURCE:
      return {
        ...state,
        activeSource: action.payload,
      };

    default:
      return state;
  }
};

export const loadSources = () => (dispatch) => {
  const defaultSource = config.dataSources[0];
  dispatch({ type: SOURCE_LIST_LOAD, payload: config.dataSources });
  dispatch({ type: SOURCE_LIST_SET_ACTIVE_SOURCE, payload: defaultSource });
};



