const videoDefaultWidth = 1920;
const videoDefaultHeight = 1080;
const videoDecreaseRate = 3; // 2 - 960*540, 3 - 640*360

export default {
  frameRate: 25,
  videoWidth: videoDefaultWidth / videoDecreaseRate,
  videoHeight: videoDefaultHeight / videoDecreaseRate,
  videoDecreaseRate,
  dataSources: [
    {
      name: 'Test video 1',
      poster: 'https://video-react.js.org/assets/poster.png',
      video: 'https://media.w3.org/2010/05/sintel/trailer_hd.mp4',
    },
  ],
};



